import request from '../utils/request';

//用户登录
export const loginByAccount = (account,imageCode) =>{
    return request({
        url:'/loginByAccount',
        method:'post',
        data:account,
        headers:{
            'Content-Type':'application/json'
        },
        params:{
            imageCode:imageCode
        }
    });
};
//用户退出
export const logout =() => {
    return request({
        url:'/logout',
        method:'get',
    });
};
//查询用户个人信息
export const getPersonalMessage = () => {
    return request({
        url:'/user/',
        method:'get'
    })
};
//条件查询课程
export const getCourse =(param) => {
    return request({
        url:'/course/',
        method:'get',
        params:param
    });
};
//简单查询课程
export const getSimpleCourse =(code,year,state) => {
    return request({
        url:'/course/user/simpleSearch',
        method:'get',
        params: {
            code:code,
            year:year,
            state:state
        }
    });
};
//根据大纲编号查询课程
export const getCourseByOutlineId =(param) => {
    return request({
        url:'/course/searchByOutlineId',
        method:'get',
        params: param
    });
};
//上传大纲
export const addOutline =(formdata) => {
    return request({
        url:'/outline/user/uploadOutline',
        method:'post',
        data:formdata,
        // 告诉jQuery不要去处理发送的数据，用于对data参数进行序列化处理 这里必须false
        processData : false,
        // 告诉jQuery不要去设置Content-Type请求头
        contentType : false,
    });
};
//上传excel批量添加课程
export const addAllCourse =(formdata) => {
    return request({
        url:'/excel/addCourse',
        method:'post',
        data:formdata,
        // 告诉jQuery不要去处理发送的数据，用于对data参数进行序列化处理 这里必须false
        processData : false,
        // 告诉jQuery不要去设置Content-Type请求头
        contentType : false,
    });
};
//上传excel批量设置课程为待修订大纲
